.list-wrap{
    display: flex;
    flex-wrap: wrap;
    height: 100vh;
    overflow-y: hidden;
    position: relative;
    &__start{
        background-color: var(--color-drak-blue);
        color: var(--color-white);
        flex: 1;
        margin-top: 9rem;
        padding: 4rem;
        @media(max-width:100em){
            margin-top: 8rem;
        }
        @media(max-width:90em){
            margin-top: 7rem;
        }
        

        span{
            font-size: 1.6rem;
            font-family: var(--font-regular);
            display: block;
            margin-top: 2rem;
            text-align: left;
            @media(max-width:100em){
               font-size: 1.4rem;
            }
            @media(max-width:100em){
                font-size: 1.3rem;
             }
        }
    }

    &__end{
        flex: 0 1 75.4rem;
        margin-top: 9rem;
        max-width: 75.4rem;
        overflow-y: auto;
        background-color: var(--color-white);
        text-align: start;
        height: calc(100vh - 9rem);
        display: none;
        @media(max-width:100em){
            flex: 0 1 65rem;
            max-width: 65rem;
            margin-top: 8rem;
        }
        @media(max-width:90em){
            flex: 0 1 59rem;
            max-width: 59rem;
            margin-top: 7rem;
        }
       .scroll-fixed{
           height: max-content;
       }

        .wrap-content{
            padding: 3rem;

            .location-details__info-group span:first-child {
                color: var(--color-black) !important;
                margin-inline-end: 1rem;
            }

            p{
                margin: 1.5rem 0;
                @media(max-width:90em){
                    font-size: 1.4rem;
                }
            }

            .readMore-content__warning{
                p{
                   margin: 0;
                }
            }
        }

        .map-section{
            img{
                width: 100%;
            }
        }

      
    }
}

